@import "../../globalSASS/global.scss";
.attention {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 100%;

    transition: 1s;

    background-color: $colorPink;

    &_show {
        transition: 1s;
        transform: translate(0, -100%);
    }

    &__wrap {
        position: relative;
        width: 100%;
        height: 100%;

    }

    &__img {
        position: absolute;
        bottom: 0;
        left: 50%;

        transform: translate(-79.4%, 0);

        width: 45.75vw;

        @media screen and (max-width: 768px) {
            transform: translate(-50%, 0);
            width: 90vw;
        }
    }

    &__text {
        position: absolute;
        top: 15%;
        right: 13.5%;

        margin: 0;
        font-size: 36 / 16 + rem;
        font-weight: bold;

        &_red {
            color: #ff0000;
        }

        &_blue {
            color: #2e41e7;
        }

        &_green {
            color: #14A511;
        }

        @media screen and (max-width: 768px) {
            top: 15%;
            left: 50%;

            transform: translate(-50%, -50%);
            font-size: 21 / 10 + rem;

            width: 180 / 10 + rem ;
        }
    }

    &__button {
        position: absolute;
        top: 57.3%;
        right: 20%;

        background-color: #ff0000;
        width: 220 / 16 + rem;
        height: 92 / 16 + rem;
        border-radius: 5px;
        transition: 0.5s;

        cursor: pointer;

        &:hover {
            transform: scale(1.1);
        }

        &:active {
            transition: 0.1s;
            transform: scale(0.9);
        }

        @media screen and (max-width: 768px) {
            top: 30%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    &__return {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-72%, -50%);

        font-size:  18 / 16 + rem;
        font-weight: bold;
        color: #fff;
        text-transform: uppercase;
    }

    &__svg {
        position: absolute;
        width: 73 / 16 + rem;
        height: 84 / 16 + rem;

        bottom: -27.5%;
        right: 0%;
    }


}