@import "../../globalSASS/global.scss";
.choice {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;

    &_theme_star {
        background-color: #000;
        background-image: url('img/stars.png');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
    }

    &__title {
        margin: 0;
        text-align: center;
        text-transform: uppercase;
        color: #fff;
        font-size: 56 / 16 + rem;

        padding-top: 35 / 16 + rem;

        &_prediction {
            text-transform: none;

            @media screen and (max-width: 768px) {
                padding-top: 15%;
            }
        }

        @media screen and (max-width: 768px) {
           font-size: 18 / 10 + rem;
        }
    }

    &__subtitle {
        position: absolute;
        left: 50%;
        bottom: 3%;
        transform: translate(-50%, -50%);

        margin: 0;
        text-align: center;
        color: #fff;
        font-size: 36 / 16 + rem;
        width: 930 * 100 / 1400 + rem;

        @media screen and (max-width: 768px) {
            font-size: 14 / 10 + rem;
            width: 290 / 10 + rem;
            text-align: left;
         }
    }
}