@import "../../globalSASS/global.scss";
.background {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	&__wrap {
		position: relative;
		width: 100%;
		height: 100%;
	}


	&__head {
		position: absolute;
		width: 11%;
		transition: 0.5s;

		&:hover {
			// transform: rotate(350deg);
			animation: background__head 0.15s infinite;

		}

		&_position_t-l {
			top: 5%;
			left: 1%;
		}

		&_position_t-r {
			top: 4%;
			right: 1.2%;

			@media screen and (max-width:768px) {
				top: 5%;
			}
		}

		&_position_b-r {
			bottom: 4%;
			right: 1%;
		}

		&_position_b-l {
			bottom: 4%;
			left: 1.2%;
		}

		@media screen and (max-width:768px) {
			width: 18%;
		}

	}

	@-webkit-keyframes background__head {
		0% {
			transform: rotate(0deg);
		}

		30% {
			transform: rotate(10deg);
		}

		60% {
			transform: rotate(0deg);
		}

		100% {
			transform: rotate(-10deg);
		}
	}

	@keyframes background__head {
		0% {
			transform: rotate(0deg);
		}

		30% {
			transform: rotate(10deg);
		}

		60% {
			transform: rotate(0deg);
		}

		100% {
			transform: rotate(-10deg);
		}
	}
}
