@import "../../globalSASS/global.scss";
.prediction {
    position: relative;

    &__return {
        position: absolute;

        top: 8.8%;
        left: 13.3%;
        transform: translate(-50%, -50%);
        display: flex;
        align-items: center;
 
        color: #fff;
        text-decoration: none;
        font-size: 24 / 16 + rem;
        font-weight: bold;
        transition: 0.5s;

        &:hover {
            transform: translate(-50%, -50%) scale(1.1);
        }

        &:active {
            transition: 0.1s;
            transform: translate(-50%, -50%) scale(0.9);
        }

        @media screen and (max-width: 768px) {
            top: 5%;
            left: 50%;
        }
    }

    &__svg {
        width:  4.5rem;
    }

    &__text {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 50vw;
        color: #fff;
        font-size: 24 / 16 + rem;
        font-weight: bold;

        transform: translate(-85.8%, -50%);

        @media screen and (max-width: 768px) {
            font-size: 12 / 10 + rem;
        }
    }

    &__img {
        position: absolute;
        top: 50%;
        right: 50%;
        border-radius: 1.45vw;
        width: 300 * 100 / 1400 + vw;

        transform: translate(170.5%, -50%);

        @media screen and (max-width: 768px) {
            width: 88 * 100 / 320 + vw;

            transform: translate(150.5%, -50%);
        }
    }

    &__link {
        margin: 0;
        position: absolute;
        bottom: 2.5%;
        left: 51.2%;
        font-size: 36 / 16 + rem;
        font-weight: bold;
        text-decoration: none;
        text-transform: uppercase;
        color: #ff0000;

        transform: translate(-50%, -50%);
        &:hover {
            text-decoration: underline;
        }

        &:active {
            color: #fff;
        }

        @media screen and (max-width: 768px) {
           font-size: 14 / 10 + rem;
           width: 158 / 10 + rem;
        }
    }
}