@import "../../globalSASS/global.scss";
.divination {
	position: relative;
	width: 100%;
	height: 100%;

	overflow: hidden;

	&_theme_pink {
		background-image: url('img/Main1920.png');
		background-size: cover;
		background-position: 50% 50%;

		@media screen and (max-width:768px) {
			background-image: url('img/Main768.jpg');
		}
	}

	&__description {
		position: absolute;
		left: 50%;
		top: 24.8%;

		font-size: 36 / 16 + rem;
		font-weight: bold;

		width: 930 / 16 + rem;

		transform: translate(-56%, 0);

		&_color_green {
			color: #14A511;
		}

		&_color_blue {
			color: #100BFF;
		}

		@media screen and (max-width: 768px) {

			left: 50%;
			top: 40%;
			transform: translate(-50%, -50%);
			font-size: 14 / 10 + rem;
			width: 280 / 10 + rem;
		}
	}

	&__attention {
		position: absolute;


		font-size: 36 / 16 + rem;
		font-weight: bold;
		color: #FF1010;
		text-decoration: underline;
		transition: 0.5s;
		cursor: default;

		
		&_future {
			left: 50%;
			top: 48%;

			transform: translate(-87.3%, 0);

			@media screen and (max-width:768px) {
				top: 52%;
				transform: translate(-50%, 0);
				font-size: 14 / 10 + rem;
				width: 280 / 10 + rem;
			}

		}

		&_popup {
			top: none;
			bottom: 7%;
			left: 50%;

			transform: translate(-50%, 0);

			cursor: pointer;

			&:hover {
				transform: translate(-50%, 0) scale(1.1);
			}
	
			&:active {
				transition: 0.1s;
				color: #fff;
			}
	
		}

		@media screen and (max-width:768px) {
			font-size: 14 / 10 + rem;
		}
	}


	&__button {
		position: absolute;
		top: 60.3%;
		right: 20%;

		width: 221 / 16 + rem;
		height: 89 / 16 + rem;

		font-size: 18 / 16 + rem;
		font-weight: bold;
		letter-spacing: 0.06rem;
		color: #fff;

		border-radius: 5px;
		background-color: #FF1E1E;
		border: none;

		transition: 0.3s;
		outline: none;
		cursor: pointer;

		&:hover {
			transform: scale(1.5);
		}

		&:active {
			transition: 0.1s;
			transform: rotate(180deg);
		}

		@media screen and (max-width: 1100px) {
			font-size: 20 / 16 + rem;
		}

		@media screen and (max-width:768px) {
			font-size: 1.1rem;
			right: 12%;
		}
	}

	&__affirmation {
		position: absolute;
		font-size: 18 / 16 + rem;
		font-weight: bold;
		text-transform: uppercase;

		transition: 0.3s;

		&_left {
			top: 48%;
			left: -1.2%;
			color: #94026B;
			transform: rotate(-90deg);

			&:hover {
				transform: rotate(-80deg);
			}
		}

		&_right {
			top: 43.5%;
			right: -2.5%;
			color: #2E41E7;
			transform: rotate(90deg);

			&:hover {
				transform: rotate(80deg);
			}

		}


		@media screen and (max-width:1100px) {
			font-size: 20 / 16 + rem;
		}

		@media screen and (max-width: 768px) {
			display: none;
		}
	}
}
