@import "../../globalSASS/global.scss";
.cards {
	display: flex;
	justify-content: space-between;
	width: 73vw;
	margin-left: auto;
	margin-right: auto;
	perspective: 800px;
	

	&_pos {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -48%);

    }
    
    &__link {
		width: 29.4%;

		@media screen and (max-width: 768px) {
			width: 31%;
		}
    }

	&__card {
		border-radius: 1.5vw;
		transition: 0.5s;
        width: 100%;
		transform-style: preserve-3d;
		animation: spin 0.5s infinity;

		outline: none;
		cursor: pointer;

		&:hover {
			transform: rotate(5deg);
			filter: drop-shadow(0 0 10px #fff);
		}

		&:active {

			transition: 0.1s;
			transform: scale(0.9);
		}

	}

	&__rotate {
		position: absolute;

		transition: 3s;
		transform:  scale(30);
		z-index: 100;
		// animation: spin 0.5s 6;
		// perspective: 1000px;

		// @-webkit-keyframes spin {

		// }

		// @keyframes spin {
		// 	0% {
		// 		transform: rotate3d(0, 180, 0, 0deg);
		// 	}

		// 	100% {
		// 		transform: rotate3d(0, 180, 0, 360deg);
		// 	}

		// }
	}

	@media screen and (max-width: 768px) {
		width: 90vw;
	}
}
